import useGlobalStore from "@/store/my-store";
import { getRandomID } from "@/utils/number";
import { PlusOutlined } from "@ant-design/icons";
import {
  Button,
  Drawer,
  Form,
  Input,
  InputNumber,
  Radio,
  Select,
  Space,
} from "antd";
import { CheckboxGroupProps } from "antd/es/checkbox/Group.js";
import FormItem from "antd/es/form/FormItem/index.js";

function AddStudent({ onClose, open, showDrawer, item }: any) {

  const state = useGlobalStore();
  return (
    <>
      <Button type="primary" onClick={showDrawer} icon={<PlusOutlined />}>
        Student qo'shish
      </Button>
      <Drawer
        title="Yangi student qo'shish"
        width={500}
        onClose={onClose}
        open={open}
        styles={{
          body: {
            paddingBottom: 80,
          },
        }}
        destroyOnClose
      >
        <Form
          onFinish={(values) => {
            const new_students = state.students.concat({
              ...values,
              id: getRandomID(),
            });
            

            onClose(false);
          }}
        >
          <FormItem
            label="Ism"
            name="name"
            rules={[{ required: true, message: "Ism kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>
          <FormItem
            label="Email"
            name="email"
            rules={[{ required: true, message: "Email kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>{" "}
          <FormItem
            label="Roli"
            name="role"
            rules={[{ required: true, message: "Roli kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>
          <FormItem>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </FormItem>
        </Form>
      </Drawer>
    </>
  );
}

export default AddStudent;
